import styled from 'styled-components';

export const Container = styled.div`
    background-color: #152126;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 100px;
    color: #cbd8dd;

    @media(max-width: 700px) {
        padding-top: 40px;
    }
`;

export const Intro = styled.div`
    display: flex;
    width: 90%;

    @media(max-width: 700px) {
        flex-direction: column-reverse;
        align-items: center;
    }
`;

export const FirstArea = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    position: relative;
    flex: 1;
`;

export const FirstAreaImg = styled.img`
    width: 70%;
    z-index: 1;
    position: absolute;

    @media(max-width: 700px) {
        position: relative;
        z-index: 100;
    }
`;

export const SecondArea = styled.div`
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: #cbd8dd;

    @media(max-width: 700px) {
        position: relative;
        z-index: 100;
    }
`;

export const SecondAreaTitle = styled.h2`
    font-size: 46px;
    margin: 30px 0 0 0;

    @media(max-width: 700px) {
        text-align: center;
        margin-bottom: 10px;
    }
`;

export const SecondAreaSubTitle = styled.h2`
    font-size: 25px;
    font-weight: normal;

    @media(max-width: 700px) {
        font-size: 20px;
        margin-bottom: 20px;
    }
`;

export const TextColor = styled.span`
    color: ${props => props.color};
`;

export const Form = styled.form`
    background-color: #1c2c33;
    display: flex;
    width: 80%;
    flex-direction: column;
    padding: 20px;
    border-radius: 6px;
    border: 5px solid #d8062d;
    margin-top: 20px;
    color: #cbd8dd;
`;

export const Input = styled.input`
    margin: 10px 0;
    background-color: inherit;
    border: 2px solid #cbd8dd;
    padding: 10px;
    color: #cbd8dd;
    outline: none;
    transition: all ease .3s;
    border-radius: 5px;

    :focus{
        border-color: #fa254a;
        box-shadow: 0 0 10px #e70730;
    }
`;

export const InputTextArea = styled.textarea`
    margin: 10px 0;
    background-color: inherit;
    border: 2px solid #cbd8dd;
    padding: 10px;
    color: #cbd8dd;
    outline: none;
    transition: all ease .3s;
    border-radius: 5px;
    min-height: 200px;
    resize: none;

    :focus{
        border-color: #fa254a;
        box-shadow: 0 0 10px #e70730;
    }
`;

export const InputText = styled.label`
    font-size: 20px;
    font-weight: bold;
    cursor: pointer;
    color: ${props => props.color};
    transition: all ease .3s;
`;

export const ButtonSubmit = styled.button`
    background-color: inherit;
    color: #cbd8dd;
    outline: none;
    border: 2px solid #cbd8dd;
    border-radius: 5px;
    font-size: 25px;
    margin-top: 10px;
    padding: 7px 40px;
    transition: all ease .4s;
    cursor: pointer;

    :hover{
        border-color: #fa506d;
        color: #fa506d;
    }
`;

export const Categories = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 20px 0 0 0;
    background-color: #cbd8dd;
    color: #152126;
    width: 100%;
`;

export const CategoriesItem = styled.div`
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #B5C0C4;
    margin: 0 5px 10px 5px;
    padding: 5px 40px;
    border-radius: 10px;
    border: 4px solid #152126;
    max-width: 280px;
    min-width: 280px;
    position: relative;

    :last-child{
        ::after{
            ${props => props.idioma == 'eng'?"content: 'Coming Soon';":"content: 'Em Breve';"}
            position: absolute;
            width: 100%;
            height: 100%;
            background-color: rgba(160, 160, 160, 0.93);
            top: 0;
            left: 0;
            border-radius: 10px;
            font-size: 50px;
            display: flex;
            align-items: center;
            justify-content: center;
            font-weight: bold;
            text-align: center;
        }
    }

    @media(max-width: 700px) {
        max-width: 90%;
        padding: 0 20px;
        box-sizing: border-box;
        margin: 10px 10px;
    }
`;

export const CategoriesTitle = styled.h2`
    color: #152126;
`;

export const CategoriesText = styled.p`
    text-align: justify;
    color: #152126;
    
    @media(max-width: 700px) {
        text-align: center;
    }
`;

export const CategoriesImg = styled.img`
    height: 100px;
`;

export const ContactArea = styled.div`
    display: flex;
    padding: 30px 0;
    width: 100%;
    justify-content: center;
    flex-wrap: wrap;
`;

export const WaveImg = styled.img``;

export const FormArea = styled.div`
    display: flex;
    width: 95%;
    padding-bottom: 50px;

    @media(max-width: 700px) {
        flex-direction: column-reverse;
    }
`;

export const FormAreaFirst = styled.div`
    display: flex;
    flex: 1;
    flex-direction: column;
    align-items: center;
`;

export const FormAreaSecond = styled.div`
    display: flex;
    flex: 1;
    flex-direction: column;
    align-items: center;
`;

export const DarksideImg = styled.img`
    width: 150px;
    margin: 0 0 30px 0;
`;

export const Badge = styled.a`
    background-color: ${props => props.color};
    width: 250px;
    border: 3px solid ${props => props.color};
    font-size: 20px;
    font-weight: bold;
    color: ${props => props.textcolor};
    text-shadow: 1px 1px 5px #000;
    padding: 10px 20px;
    text-align: center;
    border-radius: 5px;
    margin-bottom: 20px;
    cursor: pointer;
    transition: all ease .3s;
    text-decoration: none;

    :first-of-type{
        margin-top: 20px;
    }

    :hover{
        border-color: #1c2c33;
    }
`;