import styled from 'styled-components';

export const Container = styled.div`
    background-color: #cbd8dd;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 100px;

    @media(max-width: 700px) {
        padding-top: 20px;
    }
`;

export const Intro = styled.div`
    display: flex;
    width: 90%;

    @media(max-width: 700px) {
        flex-direction: column-reverse;
        align-items: center;
    }
`;

export const FirstArea = styled.div`
    display: flex;
    justify-content: center;
    flex: 1;
    position: relative;
`;

export const FirstAreaImg = styled.img`
    width: 100%;
    position: absolute;
    z-index: 1;
    filter: drop-shadow(7px 5px 2px rgba(0, 0, 0, 0.7));

    @media(max-width: 700px) {
        position: relative;
        z-index: 100;
        filter: drop-shadow(4px 5px 10px rgba(0, 0, 0, 0.8));
    }
`;

export const SecondArea = styled.div`
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: #152126;

    @media(max-width: 700px) {
        position: relative;
        z-index: 100;
    }
`;

export const SecondAreaTitle = styled.h2`
    font-size: 46px;
    margin: 50px 0 0 0;
    text-align: center;

    @media(max-width: 700px) {
        font-size: 30px;
        text-align: center;
        margin-bottom: 10px;
    }
`;

export const SecondAreaSubTitle = styled.h2`
    font-size: 25px;
    font-weight: normal;

    @media(max-width: 700px) {
        font-size: 20px;
        margin-bottom: 20px;
    }
`;

export const TextColor = styled.span`
    color: ${props => props.color};
`;

export const IntroButton = styled.button`
    background-color: transparent;
    color: #152126;
    outline: none;
    border: 2px solid #152126;
    border-radius: 5px;
    font-size: 25px;
    margin-top: 10px;
    padding: 7px 40px;
    transition: all ease .4s;
    cursor: pointer;

    :hover{
        border-color: #f20732;
        color: #f20732;
    }

    @media(max-width: 700px) {
        margin-bottom: 20px;
    }
`;

export const WaveImg = styled.img``;

export const Projects = styled.div`
    display: flex;
    width: 100%;
    flex-direction: column;
    background-color: #152126;
    align-items: center;
    height: auto;
`;

export const ProjectsContainer = styled.div`
    width: 90%;
    display: flex;
    flex-direction: column;
    height: auto;
`;

export const ProjectsContainerTitle = styled(SecondAreaTitle)`
    color: #cbd8dd;
    margin: 0;
`;

export const SearchArea = styled.div`
    display: flex;
    background-color: #203239;
    width: max-content;
    padding: 15px;
    margin: 30px 0;
    align-self: center;
    border-radius: 100px;

    @media(max-width: 700px) {
        max-width: 100%;
        flex-wrap: wrap;
        border-radius: 0;
    }
`;

export const SearchAreaItem = styled.button`
    background-color: ${props => props.color};
    color: #152126;
    font-weight: bold;
    border: 0;
    outline: none;
    padding: 10px 20px;
    border-radius: 15px;
    margin: 0 5px;
    cursor: pointer;
    border: 3px solid;
    border-color: ${props => props.bordercolor};
    transition: all ease .3s;

    @media(max-width: 700px) {
        flex: 1;
        padding: 5px 20px;
        margin-bottom: 15px;
    }
`;

export const ProjectArea = styled.div`
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    width: 100%;
    height: auto;
`;

export const ProjectItem = styled.div`
    max-width: 320px;
    min-height: 300px;
    max-height: 400px;
    margin: 20px 5px;
    padding-bottom: 20px;
    background-color: #cbd8dd;
    border: 3px solid ${props => props.color};;
    box-shadow: 0 0 20px ${props => props.color};;
    border-radius: 10px;
    display: ${props => props.show?"flex":"none"};
    flex-direction: column;
    align-items: center;
    position: relative;
    color: #152126;
`;

export const ProjectItemTitle = styled.h2``;

export const ProjectItemImg = styled.img`
    width: 80%;
    border: 2px solid #222;
`;

export const ProjectItemDesc = styled.p`
    width: 80%;
    text-align: center;
    font-size: 18px;
    line-height: 25px;
`;

export const ProjectItemBtn = styled.button`
    outline: 0;
    background-color: #152126;
    color: #cbd8dd;
    border: 2px solid #152126;
    padding: 10px 20px;
    font-size: 20px;
    border-radius: 5px;
    transition: all ease .2s;
    cursor: pointer;

    :hover{
        border-color: #f20732;
    }
`;

export const Small = styled.small`
    text-align: center;
    color: #cbd8dd;
`;