import React, { useEffect } from 'react';
import { useHistory } from 'react-router';

import CodingImg from '../../img/Coding _Two Color.svg';
import WavesImg from '../../img/wave.svg';
import SecondWavesImg from '../../img/secondwave.svg';
import LapTop from '../../img/laptop.png';

import Certificate1 from '../../img/certificates/angular.jpg';
import Certificate2 from '../../img/certificates/bootstrap.jpg';
import Certificate3 from '../../img/certificates/codeblock.jpg';
import Certificate4 from '../../img/certificates/devsuber.jpg';
import Certificate5 from '../../img/certificates/firebase.jpg';
import Certificate6 from '../../img/certificates/git.jpg';
import Certificate7 from '../../img/certificates/html5css3.jpg';
import Certificate8 from '../../img/certificates/javascript.jpg';
import Certificate9 from '../../img/certificates/stefanini.png';
import Certificate10 from '../../img/certificates/typescript.jpg';

import Carousel from "react-multi-carousel";
import "react-multi-carousel/lib/styles.css";

import './style.css';

import { Timeline, TimelineItem } from 'vertical-timeline-component-for-react';

import {

    Container,
    FirstArea,
    Intro,
    FirstAreaImg,
    SecondArea,
    SecondAreaTitle,
    SecondAreaSubTitle,
    TextColor,
    IntroButton,
    AboutMe,
    WaveImg,
    SecondWaveImg,
    AboutContainer,
    AboutFirstArea,
    AboutImg,
    AboutSecondArea,
    AboutText,
    AboutTitle,
    Formation,
    FormationContainer,
    FormationFirstArea,
    FormationSecondArea,
    FormationTitle,
    Certification,
    CertificationContainer,
    CertificationTitle,
    CertificationSlider,
    CertificationImg,
    CertificationImgArea,
    CertificationText

} from './style.js';

const HomeScreen = (props) => {

    useEffect(() => {
        props.setColor("#152126");
        props.setImg(true);
        window.scrollTo(0, 0);
    }, []);

    let history = useHistory();

    const GoToPort = () => {
        
        props.change('/portfolio');
        history.replace('/portfolio');
    
    }

    const responsive = {
        superLargeDesktop: {
          
          breakpoint: { max: 4000, min: 3000 },
          items: 5
        },
        desktop: {
          breakpoint: { max: 3000, min: 1024 },
          items: 3
        },
        tablet: {
          breakpoint: { max: 1024, min: 464 },
          items: 2
        },
        mobile: {
          breakpoint: { max: 464, min: 0 },
          items: 1
        }
    };

    return(

        <Container>

            <Intro>

                <FirstArea>

                    <FirstAreaImg draggable={false} src={CodingImg} />

                </FirstArea>

                <SecondArea>

                    <SecondAreaTitle>Darkside <TextColor color="#f20732">Programmer</TextColor></SecondAreaTitle>

                    <SecondAreaSubTitle>{'<'} Web-Mobile <TextColor color="#f20732">Developer{' / >'}</TextColor></SecondAreaSubTitle>

                    <IntroButton onClick={GoToPort}>Ver Mais</IntroButton>

                </SecondArea>

            </Intro>

            <AboutMe>
                
                <WaveImg draggable={false} src={WavesImg} />

                <AboutContainer>

                    <AboutFirstArea>

                        <AboutImg draggable={false} src={LapTop} />

                    </AboutFirstArea>

                    <AboutSecondArea>

                        <AboutTitle>Sobre <TextColor color="#f20732">Mim</TextColor></AboutTitle>

                        <AboutText>{'<'}Hello World{'>'}</AboutText>

                        <AboutText>Olá, sou o Darkside, estudante da área de programação desde 2012 e desenvolvedor web/mobile.</AboutText>

                        <AboutText>Possuo conhecimento em construção de Websites, Aplicativos e Administração de Equipes, além de ser muito apaixonado por tecnologia.</AboutText>

                        <AboutText>{'</'}Hello World{'>'}</AboutText>

                    </AboutSecondArea>

                </AboutContainer>

            </AboutMe>

            <Formation>

                <FormationContainer>

                    <FormationFirstArea>
                        
                        <FormationTitle>Minha <TextColor color="#f20732">Formação</TextColor></FormationTitle>

                        <Timeline lineColor={'#152126'} className="diminuteMargin">

                            <TimelineItem
                                key="001"
                                dateText="02/2021 - 02/2024"
                                style={{ color: '#f20732' }}
                                dateInnerStyle={{ background: '#f20732', color: '#152126' }}
                                bodyContainerStyle={{
                                    background: '#152126',
                                    padding: '20px',
                                    borderRadius: '8px',
                                    boxShadow: '0.5rem 0.5rem 2rem 0 rgba(0, 0, 0, 0.2)',
                                    color: '#cbd8dd'
                                }}
                            >
                                <h3>Análise e Desenvolvimento de Sistemas</h3>
                                <h4 style={{marginTop: "15px"}}>IFSP - Pirituba (Superior, Noturno)</h4>
                                <p>
                                    Atualmente no 2º Semestre, o curso trata de atividades como análise, projeto, desenvolvimento, gerenciamento e implantação de sistemas de informação computacionais, além de lógica de programação, inglês, programação orientada a objetos e diversos outros, para introduzir ao mercado de trabalho um verdadeiro profissional.
                                </p>
                            </TimelineItem>

                            <TimelineItem
                                key="002"
                                dateText="07/2021 - 12/2021"
                                style={{ color: '#f20732' }}
                                dateInnerStyle={{ background: '#f20732', color: '#152126' }}
                                bodyContainerStyle={{
                                    background: '#152126',
                                    padding: '20px',
                                    borderRadius: '8px',
                                    boxShadow: '0.5rem 0.5rem 2rem 0 rgba(0, 0, 0, 0.2)',
                                    color: '#cbd8dd'
                                }}
                            >
                                <h3>Proprofissão 4.0</h3>
                                <h4 style={{marginTop: "15px"}}>Instituto Proa (Profissionalizante, Matutino)</h4>
                                <p>
                                    O curso PROPROFISSÃO com foco em programação web/java é dividida em quatro núcleos:
                                    <ul>
                                        <li>Técnico: prepara o jovem para as atividades básicas de qualquer vaga de entrada no mercado de trabalho e conta com um módulo em TECNOLOGIA certificado pelo SENAC.</li>

                                        <li style={{marginTop: "15px"}}>Comportamental: desenvolve no jovem as competências sócias emocionais para conquistar e manter sua posição no mercado de trabalho</li>

                                        <li style={{marginTop: "15px"}}>Cultural: proporciona experiências práticas que enriqueçam o repertório cultural dos jovens.</li>

                                        <li style={{marginTop: "15px"}}>Prático: faz o jovem desenvolver suas competências para a vida na prática, como simulações de entrevista, visitas a empresas, projeto integrador em grupo, etc.</li>
                                    </ul>
                                </p>
                            </TimelineItem>

                            <TimelineItem
                                key="003"
                                dateText="02/2019 - 06/2020"
                                dateInnerStyle={{ background: '#152126', color: '#f20732' }}
                                bodyContainerStyle={{
                                    background: '#152126',
                                    padding: '20px',
                                    borderRadius: '8px',
                                    boxShadow: '0.5rem 0.5rem 2rem 0 rgba(0, 0, 0, 0.2)',
                                    color: '#cbd8dd'
                                }}
                            >
                                <h3>Desenvolvimento de Sistemas</h3>
                                <h4 style={{marginTop: "15px"}}>Etec Dr. Emílio Hernandez Aguilar (Técnico, Vespertino)</h4>
                                <p>
                                    O técnico tratou principalmente das seguintes áreas:
                                    <ul>
                                        <li>Lógica de Programação: Portugol, C, Java</li>
                                
                                        <li style={{marginTop: "15px"}}>Desenvolvimento Web: Html5, Css3, BootsTrap</li>

                                        <li style={{marginTop: "15px"}}>Desenvolvimento de Sistemas: PHP, C#, SQL Server, MySql</li>

                                        <li style={{marginTop: "15px"}}>Desenvolvimento Mobile: Android</li>

                                        <li style={{marginTop: "15px"}}>Conhecimentos: Qualidade e Teste de Softwares, Inglês Técnico, Scrum, OOP</li>
                                    </ul>
                                </p>
                            </TimelineItem>

                            <TimelineItem
                                key="004"
                                dateText="02/2018 - 12/2020"
                                dateInnerStyle={{ background: '#152126', color: '#f20732' }}
                                bodyContainerStyle={{
                                    background: '#152126',
                                    padding: '20px',
                                    borderRadius: '8px',
                                    boxShadow: '0.5rem 0.5rem 2rem 0 rgba(0, 0, 0, 0.2)',
                                    color: '#cbd8dd'
                                }}
                            >
                                <h3>Ensino Médio</h3>
                                <h4 style={{marginTop: "15px"}}>Etec Dr. Emílio Hernandez Aguilar</h4>
                            </TimelineItem>
                            
                        </Timeline>

                    </FormationFirstArea>

                    <FormationSecondArea>
                        
                    </FormationSecondArea>

                </FormationContainer>
                
                <SecondWaveImg draggable={false} src={SecondWavesImg} />

            </Formation>
            
            <Certification>

                <CertificationContainer>

                    <CertificationTitle>Meus <TextColor color="#f20732">Certificados</TextColor></CertificationTitle>
                    <small>(Cursos extracurriculares)</small>

                    <CertificationSlider>

                        <Carousel infinite={true} autoPlay={true} containerClass="slider" responsive={responsive}>

                            <CertificationImgArea>

                                <CertificationImg draggable={false} src={Certificate1} />

                                <CertificationText>Angular</CertificationText>

                            </CertificationImgArea>
                            
                            <CertificationImgArea>

                                <CertificationImg draggable={false} src={Certificate2} />
                                
                                <CertificationText>BootsTrap</CertificationText>

                            </CertificationImgArea>

                            <CertificationImgArea>

                                <CertificationImg draggable={false} src={Certificate3} />

                                <CertificationText>Conceitos básicos de ciências da computação</CertificationText>

                            </CertificationImgArea>

                            <CertificationImgArea>

                                <CertificationImg draggable={false} src={Certificate4} />

                                <CertificationText>Projeto DevsUber (Clone Uber - React-Native)</CertificationText>

                            </CertificationImgArea>

                            <CertificationImgArea>

                                <CertificationImg draggable={false} src={Certificate5} />

                                <CertificationText>Firebase (React-Native)</CertificationText>

                            </CertificationImgArea>

                            <CertificationImgArea>

                                <CertificationImg draggable={false} src={Certificate6} />

                                <CertificationText>Git/GitHub</CertificationText>

                            </CertificationImgArea>

                            <CertificationImgArea>

                                <CertificationImg draggable={false} src={Certificate7} />

                                <CertificationText>HTML5 {'&'} CSS3</CertificationText>

                            </CertificationImgArea>

                            <CertificationImgArea>

                                <CertificationImg draggable={false} src={Certificate8} />

                                <CertificationText>Javascript</CertificationText>

                            </CertificationImgArea>

                            <CertificationImgArea>

                                <CertificationImg draggable={false} src={Certificate9} />

                                <CertificationText>Stefanini Experience</CertificationText>

                            </CertificationImgArea>

                            <CertificationImgArea>

                                <CertificationImg draggable={false} src={Certificate10} />

                                <CertificationText>Typescript</CertificationText>

                            </CertificationImgArea>
                        
                        </Carousel>

                    </CertificationSlider>

                </CertificationContainer>

            </Certification>

        </Container>

    );

}

export default HomeScreen;