import React, { useEffect, useState } from 'react';
import { useHistory } from 'react-router';

import CodingImg from '../../img/coding.svg';
import WavesImg from '../../img/wave-dark.svg';
import SecondWavesImg from '../../img/secondwave-light.svg';

import PortfolioImg from '../../img/projects/portfolio.png';
import JatochegandoImg from '../../img/projects/jatochegando.png';
import Proa from '../../img/projects/proa.png';
import Todo from '../../img/projects/todo.png'
import Balls from '../../img/projects/balls.png';
import Snake from '../../img/projects/snake.png';
import Mine from '../../img/projects/minesweeper.png';
import Isaac from '../../img/projects/isaac.png';
import Pizzajs from '../../img/projects/pizzajs.png';
import Vd from '../../img/projects/vd.png';
import Fight from '../../img/projects/fight.png';
import Elevator from '../../img/projects/elevator.png';
import MoveOut from '../../img/projects/mo.png';
import JTCMB from '../../img/projects/jatochegandomb.png';
import RJ from '../../img/projects/ricellas.png';
import LojaDS from '../../img/projects/lojadarkside.png';
import StopWatch from '../../img/projects/stopwatch.png';
import Franzon from '../../img/projects/franzon.png';
import Peton from '../../img/projects/peton.png';

import {

    Container,
    FirstArea,
    FirstAreaImg,
    Intro,
    IntroButton,
    SecondArea,
    SecondAreaSubTitle,
    SecondAreaTitle,
    TextColor,
    WaveImg,
    Projects,
    ProjectsContainer,
    SearchArea,
    SearchAreaItem,
    ProjectsContainerTitle,
    ProjectArea,
    ProjectItem,
    ProjectItemDesc,
    ProjectItemImg,
    ProjectItemTitle,
    ProjectItemBtn,
    Small

} from './style';

function PortfolioScreen(props){

    const [search, setSearch] = useState([false, false, false, false, false]);
    const [show, setShow] = useState(true);

    let history = useHistory();

    const GoToCont = () => {
        
        props.change('/contato');
        history.replace('/contato');
    
    }

    function changeValue(index1){

        let count = 0;

        let value = search.map((value, index2) => {
            
            if(index1 == index2){
                if(!value){
                    count += 1;
                }
                return !value;                
            }
            else{
                if(value){
                    count += 1;
                }
                return value;
            }

        });

        setSearch(value);

        if(count > 0){
            setShow(false);
        }
        else{
            setShow(true);
        }

    }

    useEffect(() => {
        props.setColor("#cbd8dd");
        props.setImg(false);
        window.scrollTo(0, 0);
    }, []);

    return(
        
        <Container>
            
            <Intro>

                <FirstArea>

                    <FirstAreaImg draggable={false} src={CodingImg} />

                </FirstArea>

                <SecondArea>

                    <SecondAreaTitle>Projects and <TextColor color="#f20732">Developments</TextColor></SecondAreaTitle>

                    <SecondAreaSubTitle>{'<'} Web-Mobile <TextColor color="#f20732">Developer{' / >'}</TextColor></SecondAreaSubTitle>

                    <IntroButton onClick={GoToCont}>Free Quote</IntroButton>

                </SecondArea>

            </Intro>

            <Projects>

                <WaveImg draggable={false} src={WavesImg} />

                <ProjectsContainer>

                    <ProjectsContainerTitle>My <TextColor color="#f20732">Portfolio</TextColor></ProjectsContainerTitle>

                    <Small>Some may not be responsive</Small>

                    <SearchArea>

                        <SearchAreaItem bordercolor={search[0]?"#ccc":"#152126"} onClick={() => {changeValue(0)}} color="#ff6d00">HTML5 {'&'} CSS3</SearchAreaItem>
                        <SearchAreaItem bordercolor={search[1]?"#ccc":"#152126"} onClick={() => {changeValue(1)}} color="#f2bf22">Javascript</SearchAreaItem>
                        <SearchAreaItem bordercolor={search[2]?"#ccc":"#152126"} onClick={() => {changeValue(2)}} color="#a4a7cc">PHP</SearchAreaItem>
                        <SearchAreaItem bordercolor={search[3]?"#ccc":"#152126"} onClick={() => {changeValue(3)}} color="#00b7d5">React</SearchAreaItem>
                        <SearchAreaItem bordercolor={search[4]?"#ccc":"#152126"} onClick={() => {changeValue(4)}} color="#69529c">React-Native</SearchAreaItem>

                    </SearchArea>

                </ProjectsContainer>

                <ProjectArea>                    

                    <ProjectItem show={show || search[3]} color="#00b7d5">

                        <ProjectItemTitle>Portfolio</ProjectItemTitle>

                        <ProjectItemImg draggable={false} src={PortfolioImg} />

                        <ProjectItemDesc>Development of my own portfolio using React</ProjectItemDesc>

                        <ProjectItemBtn onClick={() => window.location='https://darkside-dev.github.io/'}>Access</ProjectItemBtn>

                    </ProjectItem>

                    <ProjectItem show={show || search[3]} color="#00b7d5">

                        <ProjectItemTitle>PROA Website Clone</ProjectItemTitle>

                        <ProjectItemImg draggable={false} src={Proa} />

                        <ProjectItemDesc>Clone of the PROA Institute Website using React</ProjectItemDesc>

                        <ProjectItemBtn onClick={() => window.location=''}>Access</ProjectItemBtn>

                    </ProjectItem>

                    <ProjectItem show={show || search[3]} color="#00b7d5">

                        <ProjectItemTitle>To-do List</ProjectItemTitle>

                        <ProjectItemImg draggable={false} src={Todo} />

                        <ProjectItemDesc>To-do List with calendar using React</ProjectItemDesc>

                        <ProjectItemBtn onClick={() => window.location='https://darkside-dev.github.io/todo-list/'}>Access</ProjectItemBtn>

                    </ProjectItem>

                    <ProjectItem show={show || search[3]} color="#00b7d5">

                        <ProjectItemTitle>Basic Fight</ProjectItemTitle>

                        <ProjectItemImg draggable={false} src={Fight} />

                        <ProjectItemDesc>Simple fight game, you can attack or defend with you character</ProjectItemDesc>

                        <ProjectItemBtn onClick={() => window.location='https://darkside-dev.github.io/fightgame/'}>Access</ProjectItemBtn>

                    </ProjectItem>

                    <ProjectItem show={show || search[4]} color="#69529c">

                        <ProjectItemTitle>Move Out</ProjectItemTitle>

                        <ProjectItemImg draggable={false} src={MoveOut} />

                        <ProjectItemDesc>Academic App (Term paper) of transport platform</ProjectItemDesc>

                        <ProjectItemBtn>- - -</ProjectItemBtn>

                    </ProjectItem>

                    <ProjectItem show={show || search[4]} color="#69529c">

                        <ProjectItemTitle>JTC Scheme</ProjectItemTitle>

                        <ProjectItemImg draggable={false} src={JTCMB} />

                        <ProjectItemDesc>Schedule app to enter and view scheduled transports for the company Já Tô Chegando</ProjectItemDesc>

                        <ProjectItemBtn>- - -</ProjectItemBtn>

                    </ProjectItem>

                    <ProjectItem show={show || search[0]} color="#ff6d00">

                        <ProjectItemTitle>Já Tô Chegando</ProjectItemTitle>

                        <ProjectItemImg draggable={false} src={JatochegandoImg} />

                        <ProjectItemDesc>Landing Page for the company Já Tô Chegando using HTML5 and CSS3</ProjectItemDesc>

                        <ProjectItemBtn onClick={() => window.location='https://jatochegando.com.br'}>Access</ProjectItemBtn>

                    </ProjectItem>

                    <ProjectItem show={show || search[1]} color="#f2bf22">

                        <ProjectItemTitle>Snake Game</ProjectItemTitle>

                        <ProjectItemImg draggable={false} src={Snake} />

                        <ProjectItemDesc>The famous snake game developed in Javascript</ProjectItemDesc>

                        <ProjectItemBtn onClick={() => window.location='https://darkside-dev.github.io/SnakeGame-JS/Cobrinha/index.html'}>Access</ProjectItemBtn>

                    </ProjectItem>

                    <ProjectItem show={show || search[1]} color="#f2bf22">

                        <ProjectItemTitle>Minesweeper</ProjectItemTitle>

                        <ProjectItemImg draggable={false} src={Mine} />

                        <ProjectItemDesc>The classic Minesweeper developed in Javascript</ProjectItemDesc>

                        <ProjectItemBtn onClick={() => window.location='https://darkside-dev.github.io/MineSweeper-JS/CampoMinado/index.html'}>Access</ProjectItemBtn>

                    </ProjectItem>

                    <ProjectItem show={show || search[1]} color="#f2bf22">

                        <ProjectItemTitle>Stopwatch</ProjectItemTitle>

                        <ProjectItemImg draggable={false} src={StopWatch} />

                        <ProjectItemDesc>Stopwatch with two color themes, blue and green, start, pause and restart options</ProjectItemDesc>

                        <ProjectItemBtn onClick={() => window.location='https://darkside-dev.github.io/Cronometro-JS--Proa/StopWatch/'}>Access</ProjectItemBtn>

                    </ProjectItem>

                    <ProjectItem show={show || search[1]} color="#f2bf22">

                        <ProjectItemTitle>Ball Clicker</ProjectItemTitle>

                        <ProjectItemImg draggable={false} src={Balls} />

                        <ProjectItemDesc>Game of clicking the red balls to turn them into blue</ProjectItemDesc>

                        <ProjectItemBtn onClick={() => window.location='https://darkside-dev.github.io/Ball-Clicker--JS/circulos/'}>Access</ProjectItemBtn>

                    </ProjectItem>

                    <ProjectItem show={show || search[0]} color="#ff6d00">

                        <ProjectItemTitle>The Binding of Isaac</ProjectItemTitle>

                        <ProjectItemImg draggable={false} src={Isaac} />

                        <ProjectItemDesc>Page made in honor of The Binding of Isaac game</ProjectItemDesc>

                        <ProjectItemBtn onClick={() => window.location='https://darkside-dev.github.io/BoI--HTML/isaac/'}>Access</ProjectItemBtn>

                    </ProjectItem>

                    <ProjectItem show={show || search[2]} color="#a4a7cc">

                        <ProjectItemTitle>Franzon Services T.I</ProjectItemTitle>

                        <ProjectItemImg draggable={false} src={Franzon} />

                        <ProjectItemDesc>Creation of a Landing Page for the company Franzon Services T.I</ProjectItemDesc>

                        <ProjectItemBtn>- - -</ProjectItemBtn>

                    </ProjectItem>

                    <ProjectItem show={show || search[1]} color="#f2bf22">

                        <ProjectItemTitle>Pizza selection</ProjectItemTitle>

                        <ProjectItemImg draggable={false} src={Pizzajs} />

                        <ProjectItemDesc>Selection of several pizzas, adding up the values at the end</ProjectItemDesc>

                        <ProjectItemBtn onClick={() => window.location='https://darkside-dev.github.io/PizzaSelect-JS/pizza/'}>Access</ProjectItemBtn>

                    </ProjectItem>

                    <ProjectItem show={show || search[0]} color="#ff6d00">

                        <ProjectItemTitle>VD Negócios</ProjectItemTitle>

                        <ProjectItemImg draggable={false} src={Vd} />

                        <ProjectItemDesc>Development of a freelance work based on a PDF</ProjectItemDesc>

                        <ProjectItemBtn onClick={() => window.location='https://darkside-dev.github.io/Vd--HTML/vdnegocios/'}>Access</ProjectItemBtn>

                    </ProjectItem>

                    <ProjectItem show={show || search[0]} color="#ff6d00">

                        <ProjectItemTitle>Computer shop</ProjectItemTitle>

                        <ProjectItemImg draggable={false} src={LojaDS} />

                        <ProjectItemDesc>Computer store project developed in group, using HTML5 and CSS3</ProjectItemDesc>

                        <ProjectItemBtn>- - -</ProjectItemBtn>

                    </ProjectItem>

                    <ProjectItem show={show || search[1]} color="#f2bf22">

                        <ProjectItemTitle>Dynamic Elevator</ProjectItemTitle>

                        <ProjectItemImg draggable={false} src={Elevator} />

                        <ProjectItemDesc>Elevator that can be controlled by keyboard or by clicking on floors</ProjectItemDesc>

                        <ProjectItemBtn onClick={() => window.location='https://darkside-dev.github.io/Elevator--JS/elevador/'}>Access</ProjectItemBtn>

                    </ProjectItem>

                    <ProjectItem show={show || search[2]} color="#a4a7cc">

                        <ProjectItemTitle>Ricellas {'&'} Jhozzas</ProjectItemTitle>

                        <ProjectItemImg draggable={false} src={RJ} />

                        <ProjectItemDesc>Pizzeria website with customer view and administrative panel (CRUD)</ProjectItemDesc>

                        <ProjectItemBtn>- - -</ProjectItemBtn>

                    </ProjectItem>

                    <ProjectItem show={show || search[2]} color="#a4a7cc">

                        <ProjectItemTitle>Pet On</ProjectItemTitle>

                        <ProjectItemImg draggable={false} src={Peton} />

                        <ProjectItemDesc>Application for registering pet records for access to services such as bathing and grooming</ProjectItemDesc>

                        <ProjectItemBtn>- - -</ProjectItemBtn>

                    </ProjectItem>

                </ProjectArea>

                <WaveImg draggable={false} src={SecondWavesImg} />

            </Projects>

        </Container>

    );

}

export default PortfolioScreen;