import React, { useEffect } from 'react';
import { useHistory } from 'react-router';

import CodingImg from '../../img/Coding _Two Color.svg';
import WavesImg from '../../img/wave.svg';
import SecondWavesImg from '../../img/secondwave.svg';
import LapTop from '../../img/laptop.png';

import Certificate1 from '../../img/certificates/angular.jpg';
import Certificate2 from '../../img/certificates/bootstrap.jpg';
import Certificate3 from '../../img/certificates/codeblock.jpg';
import Certificate4 from '../../img/certificates/devsuber.jpg';
import Certificate5 from '../../img/certificates/firebase.jpg';
import Certificate6 from '../../img/certificates/git.jpg';
import Certificate7 from '../../img/certificates/html5css3.jpg';
import Certificate8 from '../../img/certificates/javascript.jpg';
import Certificate9 from '../../img/certificates/stefanini.png';
import Certificate10 from '../../img/certificates/typescript.jpg';

import Carousel from "react-multi-carousel";
import "react-multi-carousel/lib/styles.css";

import './style.css';

import { Timeline, TimelineItem } from 'vertical-timeline-component-for-react';

import {

    Container,
    FirstArea,
    Intro,
    FirstAreaImg,
    SecondArea,
    SecondAreaTitle,
    SecondAreaSubTitle,
    TextColor,
    IntroButton,
    AboutMe,
    WaveImg,
    SecondWaveImg,
    AboutContainer,
    AboutFirstArea,
    AboutImg,
    AboutSecondArea,
    AboutText,
    AboutTitle,
    Formation,
    FormationContainer,
    FormationFirstArea,
    FormationSecondArea,
    FormationTitle,
    Certification,
    CertificationContainer,
    CertificationTitle,
    CertificationSlider,
    CertificationImg,
    CertificationImgArea,
    CertificationText

} from './style.js';

const HomeScreen = (props) => {

    useEffect(() => {
        props.setColor("#152126");
        props.setImg(true);
        window.scrollTo(0, 0);
    }, []);

    let history = useHistory();

    const GoToPort = () => {
        
        props.change('/portfolio');
        history.replace('/portfolio');
    
    }

    const responsive = {
        superLargeDesktop: {
          
          breakpoint: { max: 4000, min: 3000 },
          items: 5
        },
        desktop: {
          breakpoint: { max: 3000, min: 1024 },
          items: 3
        },
        tablet: {
          breakpoint: { max: 1024, min: 464 },
          items: 2
        },
        mobile: {
          breakpoint: { max: 464, min: 0 },
          items: 1
        }
      };

    return(

        <Container>

            <Intro>

                <FirstArea>

                    <FirstAreaImg draggable={false} src={CodingImg} />

                </FirstArea>

                <SecondArea>

                    <SecondAreaTitle>Darkside <TextColor color="#f20732">Programmer</TextColor></SecondAreaTitle>

                    <SecondAreaSubTitle>{'<'} Web-Mobile <TextColor color="#f20732">Developer{' / >'}</TextColor></SecondAreaSubTitle>

                    <IntroButton onClick={GoToPort}>View More</IntroButton>

                </SecondArea>

            </Intro>

            <AboutMe>
                
                <WaveImg draggable={false} src={WavesImg} />

                <AboutContainer>

                    <AboutFirstArea>

                        <AboutImg draggable={false} src={LapTop} />

                    </AboutFirstArea>

                    <AboutSecondArea>

                        <AboutTitle>About <TextColor color="#f20732">Me</TextColor></AboutTitle>

                        <AboutText>{'<'}Hello World{'>'}</AboutText>

                        <AboutText>Hi, I'm Darkside, programming student since 2012 and web/mobile developer. </AboutText>

                        <AboutText>I have knowledge in building Websites, Applications and Team Administration, in addition to being very passionate about technology.</AboutText>

                        <AboutText>{'</'}Hello World{'>'}</AboutText>

                    </AboutSecondArea>

                </AboutContainer>

            </AboutMe>

            <Formation>

                <FormationContainer>

                    <FormationFirstArea>
                        
                        <FormationTitle>My <TextColor color="#f20732">Formation</TextColor></FormationTitle>

                        <Timeline lineColor={'#152126'} className="diminuteMargin">

                            <TimelineItem
                                key="001"
                                dateText="02/2021 - 02/2024"
                                style={{ color: '#f20732' }}
                                dateInnerStyle={{ background: '#f20732', color: '#152126' }}
                                bodyContainerStyle={{
                                    background: '#152126',
                                    padding: '20px',
                                    borderRadius: '8px',
                                    boxShadow: '0.5rem 0.5rem 2rem 0 rgba(0, 0, 0, 0.2)',
                                    color: '#cbd8dd'
                                }}
                            >
                                <h3>Analysis and systems development</h3>
                                <h4 style={{marginTop: "15px"}}>IFSP - Pirituba (University, Night)</h4>
                                <p>
                                    Currently in the 2nd semester, the course deals with activities such as analysis, design, development, management and implementation of computer information systems, as well as programming logic, English, object-oriented programming and several others, to introduce a real professional.
                                </p>
                            </TimelineItem>

                            <TimelineItem
                                key="002"
                                dateText="07/2021 - 12/2021"
                                style={{ color: '#f20732' }}
                                dateInnerStyle={{ background: '#f20732', color: '#152126' }}
                                bodyContainerStyle={{
                                    background: '#152126',
                                    padding: '20px',
                                    borderRadius: '8px',
                                    boxShadow: '0.5rem 0.5rem 2rem 0 rgba(0, 0, 0, 0.2)',
                                    color: '#cbd8dd'
                                }}
                            >
                                <h3>Proprofissão 4.0</h3>
                                <h4 style={{marginTop: "15px"}}>Proa Institute (Professionalization, Morning)</h4>
                                <p>
                                    The PROPROFISSÃO course focused on web/java programming is divided into four cores:
                                    <ul>
                                        <li>Technician: prepares the student for the basic activities of any vacancy to enter the labor market and has a module in TECHNOLOGY certified by SENAC.</li>

                                        <li style={{marginTop: "15px"}}>Behavioral: develops in the students the emotional social skills to conquer and maintain their position in the labor market.</li>

                                        <li style={{marginTop: "15px"}}>Cultural: provides practical experiences that enrich the cultural repertoire of the students.</li>

                                        <li style={{marginTop: "15px"}}>Practical: makes the students develop their skills for practical life, such as interview simulations, visits to companies, group integrative project, etc.</li>
                                    </ul>
                                </p>
                            </TimelineItem>

                            <TimelineItem
                                key="003"
                                dateText="02/2019 - 06/2020"
                                dateInnerStyle={{ background: '#152126', color: '#f20732' }}
                                bodyContainerStyle={{
                                    background: '#152126',
                                    padding: '20px',
                                    borderRadius: '8px',
                                    boxShadow: '0.5rem 0.5rem 2rem 0 rgba(0, 0, 0, 0.2)',
                                    color: '#cbd8dd'
                                }}
                            >
                                <h3>Systems development</h3>
                                <h4 style={{marginTop: "15px"}}>Etec Dr. Emílio Hernandez Aguilar (Technician, Afternoon)</h4>
                                <p>
                                    The technician dealt mainly with the following areas:
                                    <ul>
                                        <li>Programming Logic: Portugal, C, Java</li>
                                
                                        <li style={{marginTop: "15px"}}>Web Development: Html5, Css3, BootsTrap</li>

                                        <li style={{marginTop: "15px"}}>Systems Development: PHP, C#, SQL Server, MySql</li>

                                        <li style={{marginTop: "15px"}}>Mobile Development: Android</li>

                                        <li style={{marginTop: "15px"}}>Knowledge: Software Quality and Testing, Technical English, Scrum, OOP</li>
                                    </ul>
                                </p>
                            </TimelineItem>

                            <TimelineItem
                                key="004"
                                dateText="02/2018 - 12/2020"
                                dateInnerStyle={{ background: '#152126', color: '#f20732' }}
                                bodyContainerStyle={{
                                    background: '#152126',
                                    padding: '20px',
                                    borderRadius: '8px',
                                    boxShadow: '0.5rem 0.5rem 2rem 0 rgba(0, 0, 0, 0.2)',
                                    color: '#cbd8dd'
                                }}
                            >
                                <h3>High School</h3>
                                <h4 style={{marginTop: "15px"}}>Etec Dr. Emílio Hernandez Aguilar</h4>
                            </TimelineItem>
                            
                        </Timeline>

                    </FormationFirstArea>

                    <FormationSecondArea>
                        
                    </FormationSecondArea>

                </FormationContainer>
                
                <SecondWaveImg draggable={false} src={SecondWavesImg} />

            </Formation>
            
            <Certification>

                <CertificationContainer>

                    <CertificationTitle>My <TextColor color="#f20732">Certificates</TextColor></CertificationTitle>
                    <small>(Extracurricular Courses)</small>

                    <CertificationSlider>

                        <Carousel infinite={true} autoPlay={true} containerClass="slider" responsive={responsive}>

                            <CertificationImgArea>

                                <CertificationImg draggable={false} src={Certificate1} />

                                <CertificationText>Angular</CertificationText>

                            </CertificationImgArea>
                            
                            <CertificationImgArea>

                                <CertificationImg draggable={false} src={Certificate2} />
                                
                                <CertificationText>BootsTrap</CertificationText>

                            </CertificationImgArea>

                            <CertificationImgArea>

                                <CertificationImg draggable={false} src={Certificate3} />

                                <CertificationText>Basic Concepts of Computer Science</CertificationText>

                            </CertificationImgArea>

                            <CertificationImgArea>

                                <CertificationImg draggable={false} src={Certificate4} />

                                <CertificationText>DevsUber (Uber Clone - React-Native)</CertificationText>

                            </CertificationImgArea>

                            <CertificationImgArea>

                                <CertificationImg draggable={false} src={Certificate5} />

                                <CertificationText>Firebase (React-Native)</CertificationText>

                            </CertificationImgArea>

                            <CertificationImgArea>

                                <CertificationImg draggable={false} src={Certificate6} />

                                <CertificationText>Git/GitHub</CertificationText>

                            </CertificationImgArea>

                            <CertificationImgArea>

                                <CertificationImg draggable={false} src={Certificate7} />

                                <CertificationText>HTML5 {'&'} CSS3</CertificationText>

                            </CertificationImgArea>

                            <CertificationImgArea>

                                <CertificationImg draggable={false} src={Certificate8} />

                                <CertificationText>Javascript</CertificationText>

                            </CertificationImgArea>

                            <CertificationImgArea>

                                <CertificationImg draggable={false} src={Certificate9} />

                                <CertificationText>Stefanini Experience</CertificationText>

                            </CertificationImgArea>

                            <CertificationImgArea>

                                <CertificationImg draggable={false} src={Certificate10} />

                                <CertificationText>Typescript</CertificationText>

                            </CertificationImgArea>
                        
                        </Carousel>

                    </CertificationSlider>

                </CertificationContainer>

            </Certification>

        </Container>

    );

}

export default HomeScreen;