import React, { useEffect, useState } from 'react';

import Coder from '../../img/developer.svg';
import Website from '../../img/website.svg';
import Mobile from '../../img/app.svg';
import System from '../../img/system.svg';
import Wave from '../../img/wave2.svg';
import Design from '../../img/Design.svg';
import Mentoring from '../../img/Mentoring.svg';

import {

    Container,
    FirstArea,
    Intro,
    FirstAreaImg,
    SecondArea,
    SecondAreaTitle,
    SecondAreaSubTitle,
    TextColor,
    ButtonSubmit,
    Form,
    Input,
    InputText,
    InputTextArea,
    Categories,
    CategoriesImg,
    CategoriesItem,
    CategoriesText,
    CategoriesTitle,
    ContactArea,
    WaveImg,
    FormArea,
    FormAreaFirst,
    FormAreaSecond,
    Badge,
    DarksideImg

} from './style';

function sendMessage(subject, body){

    let msg = 'oi';

    let url = `mailto:darksideprogrammer06@gmail.com?subject=${encodeURIComponent(subject)}&body=${encodeURIComponent(body)}`;

    window.location = url;

}

function ContactScreen(props){

    const [label1, setLabel1] = useState('');
    const [label2, setLabel2] = useState('');

    const [subject, setSubject] = useState('');
    const [body, setBody] = useState('');

    function PrepareMessage(){

        sendMessage(subject, body)

    }

    useEffect(() => {
        props.setColor("#152126");
        props.setImg(true);
        window.scrollTo(0, 0);
    }, []);

    function Focus(id){

        if(id == "label1"){
            setLabel1('#f20732');
        }
        else{
            setLabel2('#f20732')
        }

    }

    function Disfocus(id){

        if(id == "label1"){
            setLabel1('#cbd8dd');
        }
        else{
            setLabel2('#cbd8dd')
        }

    }

    return(

        <Container>

            <Intro>

                <FirstArea>

                    <FirstAreaImg draggable={false} src={Coder} />                    

                </FirstArea>

                <SecondArea>

                    <SecondAreaTitle>Contatos e <TextColor color="#f20732">Serviços</TextColor></SecondAreaTitle>

                    <SecondAreaSubTitle>{'<'} Web-Mobile <TextColor color="#f20732">Developer{' / >'}</TextColor></SecondAreaSubTitle>

                </SecondArea>

            </Intro>

            <Categories>

                <WaveImg src={Wave} />

                <SecondAreaTitle>Podemos <TextColor color="#f20732">Desenvolver</TextColor></SecondAreaTitle>

                <ContactArea>

                    <CategoriesItem>

                        <CategoriesTitle>Websites</CategoriesTitle>

                        <CategoriesImg src={Website} />

                        <CategoriesText>Landing pages, portfólios, sites de apresentação do seu produto e semelhantes.</CategoriesText>

                    </CategoriesItem>

                    <CategoriesItem>

                        <CategoriesTitle>Aplicativos</CategoriesTitle>

                        <CategoriesImg src={Mobile} />

                        <CategoriesText>Apps incríveis para ambos os sistemas Android / IOS.</CategoriesText>

                    </CategoriesItem>

                    <CategoriesItem>

                        <CategoriesTitle>Sistemas</CategoriesTitle>

                        <CategoriesImg src={System} />

                        <CategoriesText>Sistemas completos, com consumo de informações, conexão a banco de dados e afins.</CategoriesText>

                    </CategoriesItem>

                    <CategoriesItem>

                        <CategoriesTitle>Edições</CategoriesTitle>

                        <CategoriesImg src={Design} />

                        <CategoriesText>Criação de layouts e logos, além de edição de imagens.</CategoriesText>

                    </CategoriesItem>

                    <CategoriesItem>

                        <CategoriesTitle>Mentorias</CategoriesTitle>

                        <CategoriesImg src={Mentoring} />

                        <CategoriesText>Orientações, crescimento pessoal e profissional acerca da tecnologia.</CategoriesText>

                    </CategoriesItem>

                </ContactArea>

                <FormArea>

                    <FormAreaFirst>

                        <SecondAreaTitle>Contato</SecondAreaTitle>

                        <Badge href="https://www.linkedin.com/in/joão-victor-oliveira-arruda/" target="_blank" color="#0a66c2" textcolor="#eee">LinkedIn</Badge>
                        <Badge href="https://github.com/DarkSide-Dev" target="_blank" color="#e44d30" textcolor="#eee">Github</Badge>
                        <Badge href="mailto:darksideprogrammer06@gmail.com" target="_blank" color="#ca473a" textcolor="#eee">E-mail</Badge>
                        <Badge href="https://api.whatsapp.com/send?phone=5511998644066" target="_blank" color="#279e19" textcolor="#eee">Whatsapp</Badge>
                        
                    </FormAreaFirst>

                    <FormAreaSecond>
                        
                        <SecondAreaTitle>Faça um <TextColor color="#f20732">Orçamento</TextColor></SecondAreaTitle>

                        <Form>

                            <InputText color={label1} id="label1" for="subject">Assunto</InputText>
                            <Input value={subject} onChange={e => setSubject(e.target.value)} onFocus={() => Focus("label1")} onBlur={() => Disfocus("label1")} id="subject" name="subject" type="text" placeholder="Insira seu assunto" required />

                            <InputText color={label2} id="label2" for="desc">Descrição</InputText>
                            <InputTextArea value={body} onChange={e => setBody(e.target.value)} onFocus={() => Focus("label2")} onBlur={() => Disfocus("label2")} id="desc" name="desc" required placeholder="Insira a descrição"></InputTextArea>

                            <ButtonSubmit onClick={PrepareMessage}>Enviar</ButtonSubmit>

                        </Form>

                    </FormAreaSecond>

                </FormArea>

            </Categories>

        </Container>

    );

}

export default ContactScreen;