import React, { useEffect, useState } from 'react';
import { useHistory } from 'react-router';

import CodingImg from '../../img/coding.svg';
import WavesImg from '../../img/wave-dark.svg';
import SecondWavesImg from '../../img/secondwave-light.svg';

import PortfolioImg from '../../img/projects/portfolio.png';
import JatochegandoImg from '../../img/projects/jatochegando.png';
import Proa from '../../img/projects/proa.png';
import Todo from '../../img/projects/todo.png'
import Balls from '../../img/projects/balls.png';
import Snake from '../../img/projects/snake.png';
import Mine from '../../img/projects/minesweeper.png';
import Isaac from '../../img/projects/isaac.png';
import Pizzajs from '../../img/projects/pizzajs.png';
import Vd from '../../img/projects/vd.png';
import Fight from '../../img/projects/fight.png';
import Elevator from '../../img/projects/elevator.png';
import MoveOut from '../../img/projects/mo.png';
import JTCMB from '../../img/projects/jatochegandomb.png';
import RJ from '../../img/projects/ricellas.png';
import LojaDS from '../../img/projects/lojadarkside.png';
import StopWatch from '../../img/projects/stopwatch.png';
import Franzon from '../../img/projects/franzon.png';
import Peton from '../../img/projects/peton.png';

import {

    Container,
    FirstArea,
    FirstAreaImg,
    Intro,
    IntroButton,
    SecondArea,
    SecondAreaSubTitle,
    SecondAreaTitle,
    TextColor,
    WaveImg,
    Projects,
    ProjectsContainer,
    SearchArea,
    SearchAreaItem,
    ProjectsContainerTitle,
    ProjectArea,
    ProjectItem,
    ProjectItemDesc,
    ProjectItemImg,
    ProjectItemTitle,
    ProjectItemBtn,
    Small

} from './style';

function PortfolioScreen(props){

    const [search, setSearch] = useState([false, false, false, false, false]);
    const [show, setShow] = useState(true);

    let history = useHistory();

    const GoToCont = () => {
        
        props.change('/contato');
        history.replace('/contato');
    
    }

    function changeValue(index1){

        let count = 0;

        let value = search.map((value, index2) => {
            
            if(index1 == index2){
                if(!value){
                    count += 1;
                }
                return !value;                
            }
            else{
                if(value){
                    count += 1;
                }
                return value;
            }

        });

        setSearch(value);

        if(count > 0){
            setShow(false);
        }
        else{
            setShow(true);
        }

    }

    useEffect(() => {
        props.setColor("#cbd8dd");
        props.setImg(false);
        window.scrollTo(0, 0);
    }, []);

    return(
        
        <Container>
            
            <Intro>

                <FirstArea>

                    <FirstAreaImg draggable={false} src={CodingImg} />

                </FirstArea>

                <SecondArea>

                    <SecondAreaTitle>Projetos e <TextColor color="#f20732">Desenvolvimentos</TextColor></SecondAreaTitle>

                    <SecondAreaSubTitle>{'<'} Web-Mobile <TextColor color="#f20732">Developer{' / >'}</TextColor></SecondAreaSubTitle>

                    <IntroButton onClick={GoToCont}>Orçamento</IntroButton>

                </SecondArea>

            </Intro>

            <Projects>

                <WaveImg draggable={false} src={WavesImg} />

                <ProjectsContainer>

                    <ProjectsContainerTitle>Meu <TextColor color="#f20732">Portfólio</TextColor></ProjectsContainerTitle>

                    <Small>Alguns podem não estar responsivos</Small>

                    <SearchArea>

                        <SearchAreaItem bordercolor={search[0]?"#ccc":"#152126"} onClick={() => {changeValue(0)}} color="#ff6d00">HTML5 {'&'} CSS3</SearchAreaItem>
                        <SearchAreaItem bordercolor={search[1]?"#ccc":"#152126"} onClick={() => {changeValue(1)}} color="#f2bf22">Javascript</SearchAreaItem>
                        <SearchAreaItem bordercolor={search[2]?"#ccc":"#152126"} onClick={() => {changeValue(2)}} color="#a4a7cc">PHP</SearchAreaItem>
                        <SearchAreaItem bordercolor={search[3]?"#ccc":"#152126"} onClick={() => {changeValue(3)}} color="#00b7d5">React</SearchAreaItem>
                        <SearchAreaItem bordercolor={search[4]?"#ccc":"#152126"} onClick={() => {changeValue(4)}} color="#69529c">React-Native</SearchAreaItem>

                    </SearchArea>

                </ProjectsContainer>

                <ProjectArea>                    

                    <ProjectItem show={show || search[3]} color="#00b7d5">

                        <ProjectItemTitle>Portfólio</ProjectItemTitle>

                        <ProjectItemImg draggable={false} src={PortfolioImg} />

                        <ProjectItemDesc>Desenvolvimento do meu portfólio com React</ProjectItemDesc>

                        <ProjectItemBtn onClick={() => {props.change('/contato');history.replace('/contato');window.location='https://darkside-dev.github.io/'}}>Acessar</ProjectItemBtn>

                    </ProjectItem>

                    <ProjectItem show={show || search[3]} color="#00b7d5">

                        <ProjectItemTitle>Clone do Site PROA</ProjectItemTitle>

                        <ProjectItemImg draggable={false} src={Proa} />

                        <ProjectItemDesc>Clone do site do instituto Proa em React</ProjectItemDesc>

                        <ProjectItemBtn onClick={() => {props.change('/contato');history.replace('/contato');window.location='https://darkside-dev.github.io/CloneProa-React--Proa/'}}>Acessar</ProjectItemBtn>

                    </ProjectItem>

                    <ProjectItem show={show || search[3]} color="#00b7d5">

                        <ProjectItemTitle>Lista de Tarefas</ProjectItemTitle>

                        <ProjectItemImg draggable={false} src={Todo} />

                        <ProjectItemDesc>Lista de tarefas com calendário feito em React</ProjectItemDesc>

                        <ProjectItemBtn onClick={() => {props.change('/contato');history.replace('/contato');window.location='https://darkside-dev.github.io/todo-list/'}}>Acessar</ProjectItemBtn>

                    </ProjectItem>

                    <ProjectItem show={show || search[3]} color="#00b7d5">

                        <ProjectItemTitle>Luta Básica</ProjectItemTitle>

                        <ProjectItemImg draggable={false} src={Fight} />

                        <ProjectItemDesc>Jogo de luta simples, podendo atacar e defender seu personagem estático</ProjectItemDesc>

                        <ProjectItemBtn onClick={() => {props.change('/contato');history.replace('/contato');window.location='https://darkside-dev.github.io/fightgame/'}}>Acessar</ProjectItemBtn>

                    </ProjectItem>

                    <ProjectItem show={show || search[4]} color="#69529c">

                        <ProjectItemTitle>Move Out</ProjectItemTitle>

                        <ProjectItemImg draggable={false} src={MoveOut} />

                        <ProjectItemDesc>App acadêmico (TCC) de plataforma de transportes e mudanças</ProjectItemDesc>

                        <ProjectItemBtn>- - -</ProjectItemBtn>

                    </ProjectItem>

                    <ProjectItem show={show || search[4]} color="#69529c">

                        <ProjectItemTitle>Agenda JTC</ProjectItemTitle>

                        <ProjectItemImg draggable={false} src={JTCMB} />

                        <ProjectItemDesc>App agenda para inserir e visualizar mudanças marcadas para a empresa Já Tô Chegando</ProjectItemDesc>

                        <ProjectItemBtn>- - -</ProjectItemBtn>

                    </ProjectItem>

                    <ProjectItem show={show || search[0]} color="#ff6d00">

                        <ProjectItemTitle>Já Tô Chegando</ProjectItemTitle>

                        <ProjectItemImg draggable={false} src={JatochegandoImg} />

                        <ProjectItemDesc>Landing Page para a empresa Já Tô Chegando feito em HTML5 e CSS3</ProjectItemDesc>

                        <ProjectItemBtn onClick={() => {props.change('/contato');history.replace('/contato');window.location='https://jatochegando.com.br'}}>Acessar</ProjectItemBtn>

                    </ProjectItem>

                    <ProjectItem show={show || search[1]} color="#f2bf22">

                        <ProjectItemTitle>Snake Game</ProjectItemTitle>

                        <ProjectItemImg draggable={false} src={Snake} />

                        <ProjectItemDesc>O famoso jogo da cobrinha feito em Javascript</ProjectItemDesc>

                        <ProjectItemBtn onClick={() => {props.change('/contato');history.replace('/contato');window.location='https://darkside-dev.github.io/SnakeGame-JS/Cobrinha/index.html'}}>Acessar</ProjectItemBtn>

                    </ProjectItem>

                    <ProjectItem show={show || search[1]} color="#f2bf22">

                        <ProjectItemTitle>Campo Minado</ProjectItemTitle>

                        <ProjectItemImg draggable={false} src={Mine} />

                        <ProjectItemDesc>O clássico campo minado desenvolvido em Javascript</ProjectItemDesc>

                        <ProjectItemBtn onClick={() => {props.change('/contato');history.replace('/contato');window.location='https://darkside-dev.github.io/MineSweeper-JS/CampoMinado/index.html'}}>Acessar</ProjectItemBtn>

                    </ProjectItem>

                    <ProjectItem show={show || search[1]} color="#f2bf22">

                        <ProjectItemTitle>Cronômetro</ProjectItemTitle>

                        <ProjectItemImg draggable={false} src={StopWatch} />

                        <ProjectItemDesc>Cronômetro com dois temas de cores, azul e verde, além das opções de startar, pausar e reiniciar</ProjectItemDesc>

                        <ProjectItemBtn onClick={() => {props.change('/contato');history.replace('/contato');window.location='https://darkside-dev.github.io/Cronometro-JS--Proa/StopWatch/'}}>Acessar</ProjectItemBtn>

                    </ProjectItem>

                    <ProjectItem show={show || search[1]} color="#f2bf22">

                        <ProjectItemTitle>Ball Clicker</ProjectItemTitle>

                        <ProjectItemImg draggable={false} src={Balls} />

                        <ProjectItemDesc>Jogo de clicar nas bolinhas vermelhas, para transforma-las em azuis</ProjectItemDesc>

                        <ProjectItemBtn onClick={() => {props.change('/contato');history.replace('/contato');window.location='https://darkside-dev.github.io/Ball-Clicker--JS/circulos/'}}>Acessar</ProjectItemBtn>

                    </ProjectItem>

                    <ProjectItem show={show || search[0]} color="#ff6d00">

                        <ProjectItemTitle>The Binding of Isaac</ProjectItemTitle>

                        <ProjectItemImg draggable={false} src={Isaac} />

                        <ProjectItemDesc>Página feita em homenagem ao jogo The Binding of Isaac</ProjectItemDesc>

                        <ProjectItemBtn onClick={() => {props.change('/contato');history.replace('/contato');window.location='https://darkside-dev.github.io/BoI--HTML/isaac/'}}>Acessar</ProjectItemBtn>

                    </ProjectItem>

                    <ProjectItem show={show || search[2]} color="#a4a7cc">

                        <ProjectItemTitle>Franzon Services T.I</ProjectItemTitle>

                        <ProjectItemImg draggable={false} src={Franzon} />

                        <ProjectItemDesc>Criação de uma Landing Page para a empresa Franzon Services T.I</ProjectItemDesc>

                        <ProjectItemBtn>- - -</ProjectItemBtn>

                    </ProjectItem>

                    <ProjectItem show={show || search[1]} color="#f2bf22">

                        <ProjectItemTitle>Seleção de Pizzas</ProjectItemTitle>

                        <ProjectItemImg draggable={false} src={Pizzajs} />

                        <ProjectItemDesc>Seleção de diversas pizzas, fazendo a somatória dos valores no final</ProjectItemDesc>

                        <ProjectItemBtn onClick={() => {props.change('/contato');history.replace('/contato');window.location='https://darkside-dev.github.io/PizzaSelect-JS/pizza/'}}>Acessar</ProjectItemBtn>

                    </ProjectItem>

                    <ProjectItem show={show || search[0]} color="#ff6d00">

                        <ProjectItemTitle>VD Negócios</ProjectItemTitle>

                        <ProjectItemImg draggable={false} src={Vd} />

                        <ProjectItemDesc>Desenvolvimento de um trabalho freelancer baseado em um PDF</ProjectItemDesc>

                        <ProjectItemBtn onClick={() => {props.change('/contato');history.replace('/contato');window.location='https://darkside-dev.github.io/Vd--HTML/vdnegocios/'}}>Acessar</ProjectItemBtn>

                    </ProjectItem>

                    <ProjectItem show={show || search[0]} color="#ff6d00">

                        <ProjectItemTitle>Loja de informática</ProjectItemTitle>

                        <ProjectItemImg draggable={false} src={LojaDS} />

                        <ProjectItemDesc>Projeto loja de informática desenvolvido em grupo, utilizando HTML5 e CSS3</ProjectItemDesc>

                        <ProjectItemBtn>- - -</ProjectItemBtn>

                    </ProjectItem>

                    <ProjectItem show={show || search[1]} color="#f2bf22">

                        <ProjectItemTitle>Elevador Dinâmico</ProjectItemTitle>

                        <ProjectItemImg draggable={false} src={Elevator} />

                        <ProjectItemDesc>Elevador que pode ser controlado pelo teclado ou clicando nos andares</ProjectItemDesc>

                        <ProjectItemBtn onClick={() => {props.change('/contato');history.replace('/contato');window.location='https://darkside-dev.github.io/Elevator--JS/elevador/'}}>Acessar</ProjectItemBtn>

                    </ProjectItem>

                    <ProjectItem show={show || search[2]} color="#a4a7cc">

                        <ProjectItemTitle>Ricellas {'&'} Jhozzas</ProjectItemTitle>

                        <ProjectItemImg draggable={false} src={RJ} />

                        <ProjectItemDesc>Site de pizzaria com visualização por parte do cliente e painel administrativo (CRUD)</ProjectItemDesc>

                        <ProjectItemBtn>- - -</ProjectItemBtn>

                    </ProjectItem>

                    <ProjectItem show={show || search[2]} color="#a4a7cc">

                        <ProjectItemTitle>Pet On</ProjectItemTitle>

                        <ProjectItemImg draggable={false} src={Peton} />

                        <ProjectItemDesc>Aplicativo de cadastro de fichas de pets para acesso a serviços como banho e tosa</ProjectItemDesc>

                        <ProjectItemBtn>- - -</ProjectItemBtn>

                    </ProjectItem>

                </ProjectArea>

                <WaveImg draggable={false} src={SecondWavesImg} />

            </Projects>

        </Container>

    );

}

export default PortfolioScreen;