import styled from 'styled-components';

export const Container = styled.div`
    background-color: #152126;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 100px;

    @media(max-width: 700px) {
        padding-top: 20px;
    }
`;

export const Intro = styled.div`
    display: flex;
    width: 90%;

    @media(max-width: 700px) {
        flex-direction: column-reverse;
        align-items: center;
    }
`;

export const FirstArea = styled.div`
    display: flex;
    justify-content: center;
    flex: 1;
    position: relative;
`;

export const FirstAreaImg = styled.img`
    width: 100%;
    position: absolute;
    z-index: 1;

    @media(max-width: 700px) {
        position: relative;
        z-index: 100;
    }
`;

export const SecondArea = styled.div`
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: #cbd8dd;

    @media(max-width: 700px) {
        position: relative;
        z-index: 100;
    }
`;

export const SecondAreaTitle = styled.h2`
    font-size: 46px;
    margin: 50px 0 0 0;

    @media(max-width: 700px) {
        text-align: center;
        margin-bottom: 10px;
    }
`;

export const SecondAreaSubTitle = styled.h2`
    font-size: 25px;
    font-weight: normal;

    @media(max-width: 700px) {
        font-size: 20px;
        margin-bottom: 20px;
    }
`;

export const TextColor = styled.span`
    color: ${props => props.color};
`;

export const IntroButton = styled.button`
    background-color: transparent;
    color: #cbd8dd;
    outline: none;
    border: 2px solid #cbd8dd;
    border-radius: 5px;
    font-size: 25px;
    margin-top: 10px;
    padding: 7px 40px;
    transition: all ease .4s;
    cursor: pointer;

    :hover{
        border-color: #f20732;
        color: #f20732;
    }

    @media(max-width: 700px) {
        margin-bottom: 20px;
    }
`;

export const WaveImg = styled.img``;

export const SecondWaveImg = styled(WaveImg)`
    
`;

export const AboutMe = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    background-color: #ccd5d9;
    position: relative;
    color: #152126;
`;

export const AboutContainer = styled.div`
    display: flex;
    width: 90%;
    
    @media(max-width: 700px) {
        flex-direction: column-reverse;
        align-items: center;
    }
`;

export const AboutFirstArea = styled.div`
    flex: 1;
    display: flex;
    justify-content: center;

    @media(max-width: 700px) {
        flex-direction: column;
        align-items: center;
    }
`;

export const AboutImg = styled.img`
    width: 300px;
    height: 320px;

    @media(max-width: 700px) {
        width: 200px;
        height: 220px;
    }
`;

export const AboutSecondArea = styled.div`
    flex: 1;
    display: flex;
    flex-direction: column;
`;

export const AboutTitle = styled.h2`
    font-size: 46px;
    margin: 0;
    width: 75%;
    align-self: center;

    @media(max-width: 700px) {
        width: 100%;
        text-align: center;
    }
`;

export const AboutText = styled.p`
    color: #213038;
    font-size: 18px;
    font-weight: bold;
    width: 75%;
    align-self: center;
    line-height: 28px;    
    
    :nth-child(2){
        margin-bottom: 0;
        color: #f20732;
    }

    :nth-child(3), :nth-child(4){
        display: flex;
        padding-left: 50px;
    }

    :nth-child(5){
        margin-top: 0;
        color: #f20732;
    }

    @media(max-width: 700px) {
        width: 100%;
        text-align: center;

        :nth-child(3), :nth-child(4){
            padding-left: 0;
            text-align: justify;
        }
    }
`;

export const Formation = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    background-color: #ccd5d9;
    position: relative;
    color: #152126;
    padding-top: 50px;
`;

export const FormationContainer = styled(AboutContainer)`
    display: flex;
    width: 90%;

    @media(max-width: 700px) {
        width: 100%;
        flex-direction: column;
        align-items: center;
    }
`;

export const FormationTitle = styled.h2`
    font-size: 42px;
    margin: 0;
    width: 70%;

    @media(max-width: 700px) {
        width: 100%;
        text-align: center;
    }
`;

export const FormationFirstArea = styled.div`
    flex: 3;
    display: flex;
    flex-direction: column;
    text-align: center;
`;

export const FormationSecondArea = styled.div`
    flex: 1;
    display: flex;
`;

export const Certification = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    color: #cbd8dd;
`;

export const CertificationContainer = styled(AboutContainer)`
    flex-direction: column;
    align-items: center;
`;

export const CertificationTitle = styled(SecondAreaTitle)`
    margin: 0;
`;

export const CertificationSlider = styled.div`
    width: 100%;
`;

export const CertificationImgArea = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    position: relative;
    cursor: pointer;
    outline: none;
    transition: all ease .3s;

    :hover{
        transform: scale(1.2);
    }
`;

export const CertificationImg = styled.img`
    width: 90%;
`;

export const CertificationText = styled.p`
    margin: 0;
`;