import React, { useEffect, useState } from 'react';

import { slide as Menu } from 'react-burger-menu';

import { BrowserRouter, Switch, Link, Route } from 'react-router-dom';
import { createBrowserHistory } from 'history';
import './App.css';

import MenuDark from './assets/img/menu.png';
import MenuLight from './assets/img/menu-light.png';

import Darkside from './assets/img/logo-light.png';
import Darkside2 from './assets/img/logo.png';
import BrazilFlag from './assets/img/brazil.jpg';
import USFlag from './assets/img/usflag.png';

import Close from './assets/img/close.png';

import HomeScreen from './assets/screens/Home';
import PortfolioScreen from './assets/screens/Portfolio';
import ContactScreen from './assets/screens/Contact';

import HomeScreenEng from './assets/screens/Home/index-eng';
import PortfolioScreenEng from './assets/screens/Portfolio/index-eng';
import ContactScreenEng from './assets/screens/Contact/index-eng';

import{

  Nav,
  NavList,
  NavListItem,
  Flag,
  Logo,
  Footer,
  FooterContainer,
  FooterSection,
  FooterSectionList,
  FooterSectionListItem,
  FooterSectionText,
  FooterSectionTitle,
  FooterImg,
  MenuImg,
  LogoMenu,
  LogoMenu2

} from './style';

// REMOVER react-transition-group
function App(){

  const [history, setHistoy] = useState(createBrowserHistory().location.pathname);

  const [scrool, setScrool] = useState(true);

  const [img, setImg] = useState(true);

  const [color, setColor] = useState('#152126');

  const [lang, setLang] = useState('pt');

  function changeLang(){

    if(lang == 'pt'){
      setLang('en');
    }
    else{
      setLang('pt');
    }

  }

  window.addEventListener('scroll', () => {
    if(window.scrollY == 0){
      setScrool(true);
    }
    else{
      setScrool(false);
    }
  });

  window.addEventListener('contextmenu', (e) => {
    e.preventDefault();
  });

  return(

    <BrowserRouter basename={process.env.PUBLIC_URL}>

      <Menu itemListClassName={"menuItem"} menuClassName={"menuBody"} crossButtonClassName={"close"} burgerButtonClassName={"button"} customCrossIcon={ <MenuImg src={Close} /> } customBurgerIcon={ <MenuImg src={color=='#152126'?MenuLight:MenuDark} /> }>
        
        <Link onClick={() => {setHistoy('/')}} className={img?history=='/'?"m-list-link active":"m-list-link light":history=='/'?"m-list-link active":"m-list-link"} to="/"><LogoMenu src={Darkside} /></Link>
        
        <Link onClick={() => {setHistoy('/')}} className={img?history=='/'?"m-list-link active":"m-list-link light":history=='/'?"m-list-link active":"m-list-link"} to="/">HOME</Link>

        <Link onClick={() => {setHistoy('/portfolio')}} className={img?history=='/portfolio'?"m-list-link active":"m-list-link light":history=='/portfolio'?"m-list-link active":"m-list-link"} to="/portfolio">{lang=='pt'?'PORTFÓLIO':'PORTFOLIO'}</Link>

        <Link onClick={() => {setHistoy('/contato')}} className={img?history=='/contato'?"m-list-link active":"m-list-link light":history=='/contato'?"m-list-link active":"m-list-link"} to="/contato">{lang=='pt'?'CONTATO':'CONTACT'}</Link>

        <Link onClick={changeLang} to={history} className={img?history=='/contato'?"m-list-link":"m-list-link light":history=='/contato'?"m-list-link active":"m-list-link"}>{lang=='pt'?'PT-BR':'EN-US'} <Flag draggable={false} src={lang=='pt'?BrazilFlag:USFlag} /></Link>

      </Menu>
    
      <Nav show={scrool} color={color}>      

        <LogoMenu2 src={img?Darkside:Darkside2} />  

        <NavList>

          <NavListItem>

            <Logo draggable={false} src={img?Darkside:Darkside2} opacity={true} />

          </NavListItem>

          <NavListItem>

            <Link onClick={() => {setHistoy('/')}} className={img?history=='/'?"list-link active":"list-link light":history=='/'?"list-link active":"list-link dark"} to="/">HOME</Link>

          </NavListItem>

          <NavListItem>

            <Link onClick={() => {setHistoy('/portfolio')}} className={img?history=='/portfolio'?"list-link active":"list-link light":history=='/portfolio'?"list-link active":"list-link dark"} to="/portfolio">{lang=='pt'?'PORTFÓLIO':'PORTFOLIO'}</Link>

          </NavListItem>

          <NavListItem>

            <Link onClick={() => {setHistoy('/contato')}} className={img?history=='/contato'?"list-link active":"list-link light":history=='/contato'?"list-link active":"list-link dark"} to="/contato">{lang=='pt'?'CONTATO':'CONTACT'}</Link>

          </NavListItem>

          <NavListItem onClick={changeLang}>{lang=='pt'?'PT-BR':'EN-US'} <Flag draggable={false} src={lang=='pt'?BrazilFlag:USFlag} /></NavListItem>

        </NavList>

      </Nav>

      <Switch>

        <Route exact path="/">
          {lang=='pt'?<HomeScreen change={setHistoy} setColor={setColor} setImg={setImg} />:<HomeScreenEng change={setHistoy} setColor={setColor} setImg={setImg} />}
        </Route>
        
        <Route path="/portfolio">
          {lang=='pt'?<PortfolioScreen change={setHistoy} setColor={setColor} setImg={setImg} />:<PortfolioScreenEng change={setHistoy} setColor={setColor} setImg={setImg} />}          
        </Route>

        <Route path="/contato">
          {lang=='pt'?<ContactScreen setColor={setColor} setImg={setImg} />:<ContactScreenEng setColor={setColor} setImg={setImg} />}           
        </Route>

      </Switch>

      <Footer color={color} fontColor={color}>

        <FooterContainer>

          <FooterSection>

            <FooterSectionTitle>Darkside Programmer</FooterSectionTitle>

            <FooterSectionText>
              {lang=='pt'?'Estudante da área de programação desde 2012 e desenvolvedor web/mobile. Possuo conhecimento em construção de Websites, Aplicativos e Administração de Equipes, além de ser muito apaixonado por tecnologia.':'Programming student since 2012 and web/mobile developer. I have knowledge in building Websites, Applications and Team Administration, in addition to being very passionate about technology.'}              
            </FooterSectionText>

          </FooterSection>

          <FooterSection>

            <FooterSectionTitle>{lang=='pt'?'Institucional':'Institutional'}</FooterSectionTitle>

            <FooterSectionList>

              <FooterSectionListItem>Home</FooterSectionListItem>
              
              <FooterSectionListItem>{lang=='pt'?'Portfólio':'Portfolio'}</FooterSectionListItem>

              <FooterSectionListItem>{lang=='pt'?'Contato':'Contact'}</FooterSectionListItem>

            </FooterSectionList>

          </FooterSection>

          <FooterSection>

            <FooterSectionTitle>{lang=='pt'?'Contato':'Contact'}</FooterSectionTitle>

            <FooterSectionList>

              <FooterSectionListItem>Github: DarkSide-Dev</FooterSectionListItem>
              
              <FooterSectionListItem>E-mail: darksideprogrammer06@gmail.com</FooterSectionListItem>

              <FooterSectionListItem>
                <FooterImg draggable={false} src={img?Darkside:Darkside2} />
              </FooterSectionListItem>

            </FooterSectionList>

          </FooterSection>

        </FooterContainer>

      </Footer>

    </BrowserRouter>

  );

}

export default App;