import styled from 'styled-components';

export const Nav = styled.nav`
    background-color: ${props => props.color};
    position: fixed !important;
    display: flex;
    min-width: 100vw;
    max-width: 100vw;
    height: 50px;
    padding: 10px 0;
    z-index: 900;
    border-bottom: 2px solid;
    border-bottom-color: ${props => props.show?props.color:"#f20732;"};
    transition: all ease .5s;
`;

export const MenuImg = styled.img`
`;

export const LogoMenu = styled.img`
    width: 70%;
    z-index: 999999 !important;
`;

export const LogoMenu2 = styled.img`
    height: 100%;
    z-index: 999999 !important;
    margin: auto;
    display: none;

    @media(max-width: 700px) {
        display: flex;
    }
`;

export const NavList = styled.ul`
    display: flex;
    align-items: center;
    justify-content: flex-end;
    width: 100%;
    height: 100%;
    margin: 0;

    @media(max-width: 700px) {
        display: none;
    }
`;

export const NavListItem = styled.li`
    display: flex;
    flex-direction: column;
    height: 100%;
    justify-content: center;
    align-items: center;
    list-style: none;    
    padding: 0 ${window.innerWidth > 996?"50px":"30px"};
    position: relative;
    transition: all ease .3s;

    :first-child{
        margin-right: auto;
        padding: 0;
    }
    :last-child{
        font-size: 10px;
        color: #f20732;
        cursor: pointer;
    }
`;

export const Logo = styled.img`
    height: 50px;
    opacity: ${props=>props.opacity?"1":"0"};
`;

export const Flag = styled.img`
    height: 10px;
`;

export const Footer = styled.footer`
    width: 100%;
    max-width: 100vw;
    background-color: ${props => props.color=="#152126"?"#1C2C33":"#cbd8dd"};
    color: ${props => props.color=="#152126"?"#cbd8dd":"#152126"};
    display: flex;
    justify-content: center;    
    height: 300px;

    @media(max-width: 700px) {
        flex-direction: column;
        height: auto;
        align-items: center;
    }
`;

export const FooterContainer = styled.div`
    width: 90%;
    padding-top: 50px;
    display: flex;

    @media(max-width: 700px) {
        width: 100%;
        flex-direction: column;        
        align-items: center;
    }
`;

export const FooterSection = styled.div`
    flex: 1;
    display: flex;
    flex-direction: column;

    @media(max-width: 700px) {   
        align-items: center;
    }
`;

export const FooterSectionTitle = styled.h2`
    margin-bottom: 0;
`;

export const FooterSectionText = styled.p`
    font-size: 15px;
    line-height: 25px;
    width: 80%;

    @media(max-width: 700px) {
        text-align: justify;
    }
`;

export const FooterSectionList = styled.ul`
    list-style: none;
    padding: 0;    
`;

export const FooterSectionListItem = styled.li`
    padding-bottom: 10px;

    @media(max-width: 700px) {
        text-align: center;
        padding-bottom: 20px;
    }
`;

export const FooterImg = styled.img`
    width: 100px;
`;